<template>
  <div class="analysis-parameters">
    <el-form :model="form" label-width="120px" label-position="top" :rules="rules" ref="analysisForm">
      <!--  -->
      <template v-if="analysisType === 'trend_prediction'">
        <el-form-item label="" prop="timeRange">
          <el-select v-model="form.timeRange" placeholder="" style="width: 100%">
            <el-option label="7" value="7"></el-option>
            <el-option label="14" value="14"></el-option>
            <el-option label="30" value="30"></el-option>
            <el-option label="90" value="90"></el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="" prop="dimensions">
          <el-checkbox-group v-model="form.dimensions">
            <el-checkbox label="severity"></el-checkbox>
            <el-checkbox label="status"></el-checkbox>
            <el-checkbox label="category"></el-checkbox>
            <el-checkbox label="system"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        
        <el-form-item label="" prop="dataSources">
          <el-checkbox-group v-model="form.dataSources">
            <el-checkbox label="internal"></el-checkbox>
            <el-checkbox label="external"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        
        <el-form-item label="" prop="confidenceThreshold">
          <el-slider v-model="form.confidenceThreshold" :step="5" :marks="{
            0: '0%',
            25: '25%',
            50: '50%',
            75: '75%',
            100: '100%'
          }"></el-slider>
        </el-form-item>
      </template>
      
      <!--  -->
      <template v-if="analysisType === 'resource_optimization'">
        <el-form-item label="" prop="optimizationGoal">
          <el-radio-group v-model="form.optimizationGoal">
            <el-radio label="risk_reduction"></el-radio>
            <el-radio label="efficiency"></el-radio>
            <el-radio label="balanced"></el-radio>
          </el-radio-group>
        </el-form-item>
        
        <el-form-item label="" prop="factors">
          <el-checkbox-group v-model="form.factors">
            <el-checkbox label="cost"></el-checkbox>
            <el-checkbox label="time"></el-checkbox>
            <el-checkbox label="expertise"></el-checkbox>
            <el-checkbox label="business_impact"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        
        <el-form-item label="" prop="resourceTypes">
          <el-checkbox-group v-model="form.resourceTypes">
            <el-checkbox label="human"></el-checkbox>
            <el-checkbox label="technical"></el-checkbox>
            <el-checkbox label="financial"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        
        <el-form-item label="" prop="historyTimeRange">
          <el-select v-model="form.historyTimeRange" placeholder="" style="width: 100%">
            <el-option label="30" value="30"></el-option>
            <el-option label="90" value="90"></el-option>
            <el-option label="180" value="180"></el-option>
            <el-option label="365" value="365"></el-option>
          </el-select>
        </el-form-item>
      </template>
      
      <!--  -->
      <template v-if="analysisType === 'anomaly_detection'">
        <el-form-item label="" prop="timeRange">
          <el-select v-model="form.timeRange" placeholder="" style="width: 100%">
            <el-option label="3" value="3"></el-option>
            <el-option label="7" value="7"></el-option>
            <el-option label="14" value="14"></el-option>
            <el-option label="30" value="30"></el-option>
          </el-select>
        </el-form-item>
        
        <el-form-item label="" prop="sensitivity">
          <el-slider v-model="form.sensitivity" :min="1" :max="10" :step="1" :marks="{
            1: '',
            5: '',
            10: ''
          }"></el-slider>
        </el-form-item>
        
        <el-form-item label="" prop="anomalyTypes">
          <el-checkbox-group v-model="form.anomalyTypes">
            <el-checkbox label="spike"></el-checkbox>
            <el-checkbox label="drop"></el-checkbox>
            <el-checkbox label="pattern"></el-checkbox>
            <el-checkbox label="seasonal"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        
        <el-form-item label="" prop="scopes">
          <el-checkbox-group v-model="form.scopes">
            <el-checkbox label="all"></el-checkbox>
            <el-checkbox label="high"></el-checkbox>
            <el-checkbox label="web">Web</el-checkbox>
            <el-checkbox label="network"></el-checkbox>
            <el-checkbox label="system"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
      </template>
      
      <el-form-item label="" prop="description">
        <el-input 
          type="textarea" 
          v-model="form.description"
          :rows="3"
          placeholder="">
        </el-input>
      </el-form-item>
    </el-form>
    
    <div class="form-actions">
      <el-button @click="cancel"></el-button>
      <el-button type="primary" @click="submitForm" :loading="loading"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AnalysisParameters',
  props: {
    analysisType: {
      type: String,
      required: true,
      validator: value => ['trend_prediction', 'resource_optimization', 'anomaly_detection'].includes(value)
    }
  },
  data() {
    return {
      loading: false,
      form: {
        // 
        description: '',
        
        // 
        timeRange: '30',
        dimensions: ['severity', 'status'],
        dataSources: ['internal'],
        confidenceThreshold: 70,
        
        // 
        optimizationGoal: 'balanced',
        factors: ['cost', 'time'],
        resourceTypes: ['human', 'technical'],
        historyTimeRange: '90',
        
        // 
        sensitivity: 5,
        anomalyTypes: ['spike', 'pattern'],
        scopes: ['all']
      },
      rules: {
        // 
        timeRange: [
          { required: true, message: '', trigger: 'change' }
        ],
        dimensions: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ],
        
        // 
        optimizationGoal: [
          { required: true, message: '', trigger: 'change' }
        ],
        
        // 
        anomalyTypes: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ]
      }
    }
  },
  methods: {
    submitForm() {
      this.$refs.analysisForm.validate(valid => {
        if (valid) {
          this.loading = true
          
          // 
          const parameters = this.getParameters()
          
          // 
          this.$emit('start', parameters)
          
          // loading
        } else {
          this.$message.warning('')
          return false
        }
      })
    },
    cancel() {
      this.$emit('cancel')
    },
    getParameters() {
      // 
      const commonParams = {
        description: this.form.description
      }
      
      // 
      switch (this.analysisType) {
        case 'trend_prediction':
          return {
            ...commonParams,
            timeRange: this.form.timeRange,
            dimensions: this.form.dimensions,
            dataSources: this.form.dataSources,
            confidenceThreshold: this.form.confidenceThreshold
          }
        
        case 'resource_optimization':
          return {
            ...commonParams,
            optimizationGoal: this.form.optimizationGoal,
            factors: this.form.factors,
            resourceTypes: this.form.resourceTypes,
            historyTimeRange: this.form.historyTimeRange
          }
        
        case 'anomaly_detection':
          return {
            ...commonParams,
            timeRange: this.form.timeRange,
            sensitivity: this.form.sensitivity,
            anomalyTypes: this.form.anomalyTypes,
            scopes: this.form.scopes
          }
        
        default:
          return commonParams
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.analysis-parameters {
  padding: 10px;
  
  .form-actions {
    display: flex;
    justify-content: flex-end;
    margin-top: 30px;
    
    .el-button {
      margin-left: 10px;
    }
  }
}
</style> 