<script>
import { formatDate } from '@/utils/format'
import TrendPredictionDetail from './details/TrendPredictionDetail.vue'
import ResourceOptimizationDetail from './details/ResourceOptimizationDetail.vue'
import AnomalyDetectionDetail from './details/AnomalyDetectionDetail.vue'

export default {
  name: 'AnalysisDetail',
  components: {
    TrendPredictionDetail,
    ResourceOptimizationDetail,
    AnomalyDetectionDetail
  },
  props: {
    analysisId: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      loading: true,
      analysis: null
    }
  },
  created() {
    this.fetchAnalysisDetail()
  },
  methods: {
    formatDate,
    async fetchAnalysisDetail() {
      this.loading = true
      
      try {
        // API
        // const { data } = await this.$api.ai.getAnalysisById(this.analysisId)
        // this.analysis = data
        
        // 
        await new Promise(resolve => setTimeout(resolve, 1000))
        
        // ID
        switch (this.analysisId) {
          case '61e8f5c3b32f6c001a9b3d01':
            this.analysis = this.getTrendPredictionData()
            break
          case '61e8f5c3b32f6c001a9b3d02':
            this.analysis = this.getResourceOptimizationData()
            break
          case '61e8f5c3b32f6c001a9b3d03':
            this.analysis = this.getAnomalyDetectionData()
            break
          default:
            this.analysis = null
        }
      } catch (error) {
        this.$message.error(': ' + error.message)
        this.analysis = null
      } finally {
        this.loading = false
      }
    },
    getAnalysisTypeLabel(type) {
      const typeMap = {
        'trend_prediction': '',
        'resource_optimization': '',
        'anomaly_detection': ''
      }
      return typeMap[type] || ''
    },
    getDetailComponent() {
      if (!this.analysis) return null
      
      switch(this.analysis.type) {
        case 'trend_prediction':
          return 'trend-prediction-detail'
        case 'resource_optimization':
          return 'resource-optimization-detail'
        case 'anomaly_detection':
          return 'anomaly-detection-detail'
        default:
          return null
      }
    },
    downloadReport() {
      this.$message.info('...')
    },
    shareAnalysis() {
      this.$message.info('...')
    },
    // 
    getTrendPredictionData() {
      const now = new Date()
      const historicalData = Array.from({ length: 90 }, (_, i) => {
        const date = new Date(now)
        date.setDate(date.getDate() - 90 + i)
        return {
          date,
          count: 10 + Math.floor(Math.random() * 40),
          label: i % 7 === 0 ? '' : undefined
        }
      })
      
      return {
        id: '61e8f5c3b32f6c001a9b3d01',
        title: '30',
        type: 'trend_prediction',
        description: '30',
        confidence: 0.85,
        createdAt: new Date(Date.now() - 86400000 * 2),
        recommendations: [
          '',
          '',
          '',
          ''
        ],
        analysisData: {
          timeRange: '30',
          predictedCounts: {
            '': 120,
            '': 180,
            '': 90,
            '': 45
          },
          trendFactors: [
            {
              factor: '',
              impact: 0.25,
              confidence: 0.85,
              description: ''
            },
            {
              factor: '',
              impact: -0.15,
              confidence: 0.78,
              description: ''
            },
            {
              factor: '',
              impact: 0.30,
              confidence: 0.92,
              description: ''
            },
            {
              factor: '',
              impact: -0.18,
              confidence: 0.75,
              description: ''
            }
          ],
          historicalData
        }
      }
    },
    getResourceOptimizationData() {
      return {
        id: '61e8f5c3b32f6c001a9b3d02',
        title: '',
        type: 'resource_optimization',
        description: '',
        confidence: 0.78,
        createdAt: new Date(Date.now() - 86400000 * 5),
        recommendations: [
          '',
          '',
          '',
          ''
        ],
        analysisData: {
          currentAllocation: {
            '': 30,
            '': 25,
            '': 20,
            '': 15,
            '': 10
          },
          recommendedAllocation: {
            '': 40,
            '': 25,
            '': 10,
            '': 20,
            '': 5
          },
          potentialImprovement: 25.5,
          bottlenecksIdentified: [
            {
              area: '',
              severity: '',
              description: '',
              solution: ''
            },
            {
              area: '',
              severity: '',
              description: '',
              solution: ''
            },
            {
              area: '',
              severity: '',
              description: '',
              solution: ''
            }
          ]
        }
      }
    },
    getAnomalyDetectionData() {
      return {
        id: '61e8f5c3b32f6c001a9b3d03',
        title: '7',
        type: 'anomaly_detection',
        description: '',
        confidence: 0.82,
        createdAt: new Date(Date.now() - 86400000 * 1),
        recommendations: [
          '',
          'Web',
          '',
          ''
        ],
        analysisData: {
          anomaliesDetected: [
            {
              type: '',
              severity: '',
              description: 'Web',
              detectedAt: new Date(Date.now() - 86400000 * 2),
              affectedArea: 'Web',
              score: 0.92
            },
            {
              type: '',
              severity: '',
              description: '',
              detectedAt: new Date(Date.now() - 86400000 * 5),
              affectedArea: '',
              score: 0.78
            },
            {
              type: '',
              severity: '',
              description: '',
              detectedAt: new Date(Date.now() - 86400000 * 3),
              affectedArea: '',
              score: 0.65
            }
          ],
          timeRange: '7',
          baselineData: {
            dailyAverage: 8.5,
            weeklyTrend: '',
            normalRange: [5.0, 12.0]
          }
        }
      }
    }
  }
}
